.btn {
  display: inline-flex;
  text-align: center;
  justify-content: center;
  color: #a5803f;
  padding: 10px 25px;
  background: transparent;
  cursor: pointer;
  outline: none;
  position: relative;
  font-size: 13px;
  transition: all 0.3s linear 0s;
  text-decoration: none;
  text-transform: uppercase;
  outline: none;
  position: relative !important;
  z-index: 1;
  font-weight: 500;
  letter-spacing: 0.05em;
  min-height: 58px;
  align-items: center;
}

.btn.btn-bordered {
  color: #fff;
  min-width: 235px;
  border: 2px solid #a5803f;
}

.btn.btn-bordered:hover {
  background: #a5803f;
  color: #fff;
}

.btn.btn-bordered.dark-color {
  color: #000;
}

.btn.btn-bordered.dark-color:hover {
  color: #fff;
}

.btn.btn-filled {
  color: #fff;
  background: #a5803f;
  min-width: 235px;
  border: 2px solid #a5803f;
}

.btn.btn-filled:hover {
  background: none;
  color: #24222e;
}

.btn.btn-filled.white-color:hover {
  color: #fff;
}

input.btn,
button.btn {
  display: inline-block;
}

