.custom-checkbox {
  margin: 20px 0;
}

.custom-checkbox input[type=checkbox] {
  position: absolute;
  right: -999999px;
  z-index: -999999;
  display: none;
}

.custom-checkbox input[type=checkbox]:checked + label:before {
  background-image: url(../img/sprite.png);
  background-position: 0px 0px;
  width: 23px;
  height: 23px;
}

.custom-checkbox label {
  min-height: 23px;
  display: inline-flex;
  align-items: center;
  flex-wrap: wrap;
  font-size: 16px;
  position: relative;
  padding-left: 50px;
  line-height: 1.2;
  color: #fff;
  font-weight: normal;
}

@media (max-width: 567px) {
  .custom-checkbox label {
    font-size: 15px;
  }
}

.custom-checkbox label:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  background-image: url(../img/sprite.png);
  background-position: -26px 0px;
  width: 23px;
  height: 23px;
}

