/* ==========================================================================
   STYLES FOR UNDERLINE LIST
   ========================================================================== */
.underline-list {
  list-style: none;
  padding: 0 0 0 30px;
  margin: 25px 0;
}

.underline-list li {
  position: relative;
  color: rgba(54, 54, 54, 0.9);
  font-size: 14px;
  margin-bottom: 20px;
}

.underline-list li span {
  border-bottom: solid 1px rgba(54, 54, 54, 0.9);
}

@media (min-width: 768px) {
  .underline-list li {
    font-size: 15px;
  }
}

@media (min-width: 992px) {
  .underline-list li {
    font-size: 14px;
  }
}

@media (min-width: 1400px) {
  .underline-list li {
    font-size: 15px;
  }
}

.underline-list li:last-child {
  margin: 0;
}

.underline-list li:before {
  content: '';
  width: 9px;
  height: 9px;
  border-radius: 10px;
  border: solid 1px #9a8f67;
  display: block;
  position: absolute;
  left: -30px;
  top: 5px;
}

/* ==========================================================================
   STYLES FOR CUSTOM LIST
   ========================================================================== */
.custom-list {
  list-style: none;
  padding: 0;
  margin: 30px 0;
}

.custom-list li {
  position: relative;
  color: rgba(54, 54, 54, 0.9);
