.responsive-table table {
  min-width: 1000px;
}

table {
  width: 100%;
  max-width: 100%;
  background-color: transparent;
  border-collapse: collapse;
  color: #363636;
  border: solid 2px #e1d8c7;
}

table th {
  background: rgba(154, 127, 69, 0.04);
  text-align: center;
  font-weight: 500;
  color: #ae9566;
  padding: 25px;
  border: 1px solid #e3e3e3;
